<template>
  <div>
    <nav-bar></nav-bar>
    <div class="tips">
      <h1>Please use this function on PC!</h1>
    </div>
    <div class="comb-container">
      <div class="col-xs-12 col-md-2 tool-bar">
        <h4 id="select-title">Select comb:</h4>
        <div class='combList' v-for="comb in combNameArray" :key="comb.netId">
          <a class='comb-name' @click="getComb(comb.netId,comb.combName)">
            {{comb.combName}}
            <span class="glyphicon glyphicon-chevron-right select-icon" v-if="combSelectName===comb.combName"></span>
          </a>
        </div>
        <a>
          <div id="add-button" data-toggle="modal" data-target="#addComb_modal" type="button">+ Add Comb</div>
        </a>
        <div class="modal fade" id="addComb_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="exampleModalLabel">Add Comb</h4>
              </div>
              <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Comb name:</label>
                    <input type="text" class="form-control" id="combName1" placeholder="Comb name should not be empty">
                  </div>
                </form>
              </div>
              <div class="modal-footer">
                <button type="button" id="close4" class="btn btn-primary" data-dismiss="modal" @click="closeComb">Close</button>
                <button type="button" class="btn btn-primary" @click="addComb">Submit</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-xs-12 col-md-10 comb">

        <div v-if="this.combSelect" class="op-area">
          <strong>Node:</strong>
          <a>
            <span id="add-icon" class="glyphicon glyphicon-plus-sign" data-toggle="modal" data-target="#addNode_modal" type="button"></span>
          </a>
          <a>
            <span id="minus-icon" class="glyphicon glyphicon-minus-sign" data-toggle="modal" data-target="#deleteNode_modal" type="button"></span>
          </a>
          <strong>Link:</strong>
          <a>
            <span id="add-icon" class="glyphicon glyphicon-plus-sign" data-toggle="modal" data-target="#addLink_modal" type="button"></span>
          </a>
          <a>
            <span id="minus-icon" class="glyphicon glyphicon-minus-sign" data-toggle="modal" data-target="#deleteLink_modal" type="button"></span>
          </a>
          <button type="button" class="btn btn-primary out-button" @click="SaveComb">Save</button>
          <button type="button" class="btn btn-primary out-button" @click="DeleteComb">Delete</button>
        </div>
        <div class="modal fade" id="addNode_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="exampleModalLabel">Add Net-node</h4>
              </div>
              <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Node name:</label>
                    <input type="text" class="form-control" id="name1" placeholder="Paper title/Author name">
                  </div>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Node radius:</label>
                    <input type="text" class="form-control" id="radius1" placeholder="Node radius here">
                  </div>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Node color:</label>
                    <input type="radio" class="radioColor" name="radioColor" value="black" checked>Black
                    <input type="radio" class="radioColor" name="radioColor" value="red">Red
                    <input type="radio" class="radioColor" name="radioColor" value="blue">Blue
                    <input type="radio" class="radioColor" name="radioColor" value="green">Green
                    <input type="radio" class="radioColor" name="radioColor" value="yellow">Yellow
                    <input type="radio" class="radioColor" name="radioColor" value="purple">Purple
                  </div>
                </form>
              </div>
              <div class="modal-footer">
                <button type="button" id="close1" class="btn btn-primary" data-dismiss="modal" @click="closeNode">Close</button>
                <button type="button" class="btn btn-primary" @click="addNode">Submit</button>
              </div>
            </div>
          </div>
        </div>
        <div class="modal fade" id="deleteNode_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="exampleModalLabel">Delete Net-node</h4>
              </div>
              <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Node name:</label>
                    <input type="text" class="form-control" id="name2" placeholder="Paper title/Author name">
                  </div>
                </form>
              </div>
              <div class="modal-footer">
                <button type="button" id="close2" class="btn btn-primary" data-dismiss="modal" @click="closeNode">Close</button>
                <button type="button" class="btn btn-primary" @click="deleteNode">Submit</button>
              </div>
            </div>
          </div>
        </div>
        <div class="modal fade" id="addLink_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="exampleModalLabel">Add Net-link</h4>
              </div>
              <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Start Node Name:</label>
                    <input type="text" class="form-control" id="startNode1" placeholder="Paper title/Author name">
                  </div>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">End Node name:</label>
                    <input type="text" class="form-control" id="endNode1" placeholder="Paper title/Author name">
                  </div>
                </form>
              </div>
              <div class="modal-footer">
                <button type="button" id="close3" class="btn btn-primary" data-dismiss="modal" @click="closeLink">Close</button>
                <button type="button" class="btn btn-primary" @click="addLink">Submit</button>
              </div>
            </div>
          </div>
        </div>
        <div class="modal fade" id="deleteLink_modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel">
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="exampleModalLabel">Delete Net-link</h4>
              </div>
              <div class="modal-body">
                <form>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">Start Node Name:</label>
                    <input type="text" class="form-control" id="startNode2" placeholder="Paper title/Author name">
                  </div>
                  <div class="form-group">
                    <label for="recipient-name" class="control-label">End Node name:</label>
                    <input type="text" class="form-control" id="endNode2" placeholder="Paper title/Author name">
                  </div>
                </form>
              </div>
              <div class="modal-footer">
                <button type="button" id="close4" class="btn btn-primary" data-dismiss="modal" @click="closeLink">Close</button>
                <button type="button" class="btn btn-primary" @click="deleteLink">Submit</button>
              </div>
            </div>
          </div>
        </div>
        <svg id="net-background" width="1250" height="600">
          <text x="20" y="590" fill="black" font-size="20">{{this.title}}</text>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import * as d3 from 'd3'
import navBar from '../main/NavBar'
var initnode = []
var initlink = []
var svgNode
var svgLink
var simulation

export default {
  name: 'UserComb',
  data () {
    return {
      id: this.data.id,
      combSelectId: null,
      combSelectName: null,
      title: null,
      combSelect: false,
      combNameArray: []
    }
  },
  components: {
    navBar
  },
  methods: {
    selectComb: function (d) {

    },
    reconstruct: function () {
      console.log(initnode)
      console.log(initlink)
      let that = this
      simulation.nodes(initnode).on('tick', this.ticked)
      simulation.force('link').links(initlink)

      svgLink = svgLink.data([])
      svgNode = svgNode.data([])

      svgNode.exit().remove()
      svgLink.exit().remove()

      svgLink = svgLink.data(initlink)
      svgNode = svgNode.data(initnode)

      svgLink = svgLink
        .enter()
        .append('line')
        .attr('stroke-width', function (d) {
          return Math.sqrt(d.value)
        })
        .attr('stroke', function (d) {
          return '#999'
        })

      svgNode = svgNode
        .enter()
        .append('circle')
        .attr('r', function (d) {
          return d.radius
        })
        .attr('fill', function (d, i) {
          return d.nodeColor
        })
        .call(
          d3
            .drag()
            .on('start', this.dragstarted)
            .on('drag', this.dragged)
            .on('end', this.dragended)
        )
        .on('mouseover', function (d, i) {
          that.title = d.id
        })

      simulation.alpha(1).restart()
    },
    ticked: function () {
      svgLink
        .attr('x1', function (d) {
          return d.source.x
        })
        .attr('y1', function (d) {
          return d.source.y
        })
        .attr('x2', function (d) {
          return d.target.x
        })
        .attr('y2', function (d) {
          return d.target.y
        })

      svgNode
        .attr('cx', function (d) {
          return d.x
        })
        .attr('cy', function (d) {
          return d.y
        })
    },
    dragstarted: function (d) {
      if (!d3.event.active) simulation.alphaTarget(0.3).restart()
      d.fx = d.x
      d.fy = d.y
    },
    dragged: function (d) {
      d.fx = d3.event.x
      d.fy = d3.event.y
    },
    dragended: function (d) {
      if (!d3.event.active) simulation.alphaTarget(0)
      d.fx = null
      d.fy = null
    },
    getComb: function (netId, combName) {
      if (this.combSelectName === combName) {
        return
      }

      let that = this
      this.combSelect = true
      this.combSelectName = combName
      this.combSelectId = netId
      this.title = null
      initnode.splice(0, initnode.length)
      initlink.splice(0, initlink.length)

      this.$http.post('/getnet', { netId: netId })
        .then((res) => {
          if (res.data.userId !== null) {
            var resNodeArray = res.data.nodeArray
            var resLinkArray = res.data.linkArray
            for (var i = 0; i < resNodeArray.length; i++) {
              var node = {}
              node.id = resNodeArray[i].nodeId
              node.nodeColor = resNodeArray[i].nodeColor
              node.radius = resNodeArray[i].radius
              initnode.push(node)
            }

            initlink = resLinkArray
            that.reconstruct()
          } else {
            alert('Get net error!')
          }
        })
    },
    addComb: function () {
      let that = this
      var combName = $('#combName1').val()
      if (combName === '') {
        return
      }

      if (this.combNameArray.length > 5) {
        alert('You can have 6 combs at most!')
        return
      }

      var errorFlag = false

      for (var i = 0; i < this.combNameArray.length; i++) {
        if (combName === this.combNameArray[i].combName) {
          errorFlag = true
          break
        }
      }

      if (errorFlag) {
        alert('Same name comb already exists!')
        return
      }

      var newComb = {}
      newComb.combName = combName
      console.log(combName)
      this.$http.post('./addnet', { userId: this.id, name: combName, description: '', nodeArray: [], listArray: [] })
        .then((res) => {
          newComb.netId = res.data.netId
          that.combNameArray.push(newComb)
          console.log(that.combNameArray)
          that.closeComb()
          $('#close4').click()
        })
    },
    SaveComb: function () {
      var sendNodeArray = []
      var sendLinkArray = []
      for (var i = 0; i < initnode.length; i++) {
        var newNode = {}
        newNode.nodeId = initnode[i].id
        newNode.nodeColor = initnode[i].nodeColor
        newNode.radius = initnode[i].radius
        sendNodeArray.push(newNode)
      }

      for (var j = 0; j < initlink.length; j++) {
        var newLink = {}
        newLink.linkId = initlink[j].linkId
        newLink.source = initlink[j].source.id
        newLink.target = initlink[j].target.id
        newLink.value = initlink[j].value
        sendLinkArray.push(newLink)
      }
      console.log(sendNodeArray)
      console.log(sendLinkArray)

      this.$http.post('./updatenet', { netId: this.combSelectId, name: this.combSelectName, description: '', nodeArray: sendNodeArray, linkArray: sendLinkArray })
        .then((res) => {
          if (res.data.netId !== null) {
            alert('Save successful!')
          }
        })
    },
    DeleteComb: function () {
      let that = this
      var index = null
      for (var i = 0; i < this.combNameArray.length; i++) {
        if (this.combNameArray[i].combName === this.combSelectName) {
          index = i
          break
        }
      }
      this.combSelect = null
      this.combSelectName = false
      this.title = null
      this.combNameArray.splice(index, 1)
      svgLink = svgLink.data([])
      svgNode = svgNode.data([])

      svgNode.exit().remove()
      svgLink.exit().remove()

      this.$http.post('/deletenet', { netId: this.combSelectId })
        .then((res) => {
          that.combSelectId = null
        })
    },
    closeComb: function () {
      $('#combName1').val('')
    },
    closeNode: function () {
      $('#name1').val('')
      $('#name2').val('')
      $('#radius1').val('')
      $('#name1').parent().removeClass('has-error')
      $('#name2').parent().removeClass('has-error')
      $('#radius1').parent().removeClass('has-error')
      $('#name1-help').remove()
      $('#radius1-help').remove()
      $('#name2-help').remove()
    },
    addNode: function () {
      // var color = d3.scaleOrdinal(d3.schemeCategory10)
      var Nodename = $('#name1').val()
      var radius = $('#radius1').val()
      var colorName = document.getElementsByName('radioColor')
      var errorFlag = false
      var isNum = /^[0-9]*$/

      $('#name1-help').remove()
      $('#radius1-help').remove()
      $('#name1').parent().removeClass('has-error')
      $('#radius1').parent().removeClass('has-error')

      if (!isNum.test(radius)) {
        radius = ''
      }

      if (Nodename === '') {
        $('#name1').parent().addClass('has-error')
        $('#name1').after('<span id="name1-help" class="help-block">Node name can not be empty</span>')
        errorFlag = true
      }

      if (radius === '') {
        $('#radius1').parent().addClass('has-error')
        $('#radius1').after('<span id="radius1-help" class="help-block">Node radius format error</span>')
        errorFlag = true
      }

      if (errorFlag) {
        return
      }

      var Nodecolor = 'black'

      for (var i = 0; i < colorName.length; i++) {
        if (colorName[i].checked) {
          Nodecolor = colorName[i].value
        }
      }

      var node = {}
      node.id = Nodename
      node.radius = radius
      node.nodeColor = Nodecolor
      initnode.push(node)

      this.reconstruct()
      $('#close1').click()
    },
    deleteNode: function () {
      var Nodename = $('#name2').val()
      var errorFlag = false
      var everApear = false
      var index = null

      $('#name2-help').remove()
      $('#name2').parent().removeClass('has-error')

      for (var i = 0; i < initnode.length; i++) {
        if (Nodename === initnode[i].id) {
          everApear = true
          index = i
          break
        }
      }

      if (!everApear) {
        Nodename = ''
      }
      if (Nodename === '') {
        $('#name2').parent().addClass('has-error')
        $('#name2').after('<span id="name2-help" class="help-block">Node name can not be found in net</span>')
        errorFlag = true
      }

      if (errorFlag) {
        return
      }

      if (Nodename === this.title) {
        this.title = null
      }

      initnode.splice(index, 1)
      var hasApear = false
      var count = 0
      var linkIndex = null

      for (var j = 0; j < initlink.length; j++) {
        if (Nodename === initlink[j].source.id || Nodename === initlink[j].target.id) {
          count++
          if (!hasApear) {
            hasApear = true
            linkIndex = j
          }
        }
      }

      initlink.splice(linkIndex, count)
      this.reconstruct()
      $('#close2').click()
    },

    addLink: function (d) {
      var startNode = $('#startNode1').val()
      var endNode = $('#endNode1').val()
      var errorFlag = false
      var errorStartNode = true
      var errorEndNode = true

      $('#startNode1-help').remove()
      $('#endNode1-help').remove()
      $('#startNode1').parent().removeClass('has-error')
      $('#endNode1').parent().removeClass('has-error')

      for (var i = 0; i < initnode.length; i++) {
        if (startNode === initnode[i].id) {
          errorStartNode = false
        }
        if (endNode !== startNode && endNode === initnode[i].id) {
          errorEndNode = false
        }
      }

      for (var j = 0; j < initlink.length; j++) {
        if (startNode === initlink[j].source.id && endNode === initlink[j].target.id) {
          errorStartNode = true
          errorEndNode = true
          break
        }

        if (startNode === initlink[j].target.id && endNode === initlink[j].source.id) {
          errorStartNode = true
          errorEndNode = true
          break
        }
      }

      if (errorStartNode) {
        startNode = ''
      }

      if (errorEndNode) {
        endNode = ''
      }

      if (startNode === '') {
        $('#startNode1').parent().addClass('has-error')
        $('#startNode1').after('<span id="startNode1-help" class="help-block">Node name can not be found or link already exists</span>')
        errorFlag = true
      }

      if (endNode === '') {
        $('#endNode1').parent().addClass('has-error')
        $('#endNode1').after('<span id="endNode1-help" class="help-block">Node name can not be found or link already exists</span>')
        errorFlag = true
      }

      if (errorFlag) {
        return
      }

      console.log(startNode)
      console.log(endNode)
      console.log(initnode)
      var link = {}
      link.source = startNode
      link.target = endNode
      link.value = 2
      if (initlink.length === 0) {
        link.linkId = 0
      } else {
        link.linkId = initlink[initlink.length - 1].linkId + 1
      }
      initlink.push(link)

      this.reconstruct()
      $('#close3').click()
    },
    deleteLink: function (d) {
      var startNode = $('#startNode2').val()
      var endNode = $('#endNode2').val()
      var errorFlag = false
      var everApear = false
      var index = null
      $('#startNode2-help').remove()
      $('#endNode2-help').remove()
      $('#startNode2').parent().removeClass('has-error')
      $('#endNode2').parent().removeClass('has-error')

      for (var i = 0; i < initlink.length; i++) {
        if (startNode === initlink[i].source.id && endNode === initlink[i].target.id) {
          index = i
          everApear = true
          break
        }

        if (endNode === initlink[i].source.id && startNode === initlink[i].target.id) {
          index = i
          everApear = true
          break
        }
      }

      if (!everApear) {
        startNode = ''
        endNode = ''
      }

      if (startNode === '') {
        $('#startNode2').parent().addClass('has-error')
        $('#startNode2').after('<span id="startNode2-help" class="help-block">Link can not be found</span>')
        errorFlag = true
      }

      if (endNode === '') {
        $('#endNode2').parent().addClass('has-error')
        $('#endNode2').after('<span id="endNode2-help" class="help-block">Link can not be found</span>')
        errorFlag = true
      }

      if (errorFlag) {
        return
      }

      initlink.splice(index, 1)

      this.reconstruct()
      $('#close4').click()
    },

    closeLink: function () {
      $('#startNode1').val('')
      $('#endNode1').val('')
      $('#startNode2').val('')
      $('#endNode2').val('')
      $('#startNode1').parent().removeClass('has-error')
      $('#endNode1').parent().removeClass('has-error')
      $('#startNode2').parent().removeClass('has-error')
      $('#endNode2').parent().removeClass('has-error')
      $('#startNode1-help').remove()
      $('#endNode1-help').remove()
      $('#startNode2-help').remove()
      $('#endNode2-help').remove()
    }
  },
  mounted () {
    $('canvas').remove()
    let that = this
    initnode.splice(0, initnode.length)
    initlink.splice(0, initlink.length)
    // 自定义引力

    var svg = d3.select('svg')
    var width = svg.attr('width')
    var height = svg.attr('height')
    var repelForce = d3
      .forceManyBody()
      .strength(-800)
      .distanceMax(200)
    // 自定义斥力

    simulation = d3
      .forceSimulation()
      .force(
        'link',
        d3.forceLink().id(function (d) {
          return d.id
        })
      )
      .force('center', d3.forceCenter(width / 2, height / 2))
      // .force('attractForce',attractForce)
      .force('repelForce', repelForce)

    svgLink = svg
      .append('g')
      .attr('class', 'links')
      .selectAll('line')

    svgNode = svg
      .append('g')
      .attr('class', 'nodes')
      .selectAll('circle')

    simulation.nodes(initnode).on('tick', this.ticked)
    simulation.force('link').links(initlink)

    svgLink = svgLink
      .data(initlink)
      .enter()
      .append('line')
      .attr('stroke-width', function (d) {
        return Math.sqrt(d.value)
      })

    svgNode = svgNode
      .data(initnode)
      .enter()
      .append('circle')
      .attr('r', function (d, i) {
        return d.radius
      })
      .attr('fill', function (d, i) {
        return '#2c3e50'
      })
      .call(
        d3
          .drag()
          .on('start', this.dragstarted)
          .on('drag', this.dragged)
          .on('end', this.dragended)
      )
      .on('mouseover', function (d, i) {
        this.title = d.id
      })

    this.$http.post('/usernet', { userId: this.id })
      .then((res) => {
        for (var i = 0; i < res.data.nets.length; i++) {
          var singleComb = {}
          singleComb.combName = res.data.nets[i].name
          singleComb.netId = res.data.nets[i].netId
          that.combNameArray.push(singleComb)
        }
      })
  }
}
</script>

<style scoped>
#select-title {
  color: aliceblue;
  font-size: 20px;
}
.op-area a {
  color: black;
}
.comb-container {
  width: 100%;
  position: absolute;
  top: 70px;
  bottom: 0px;
  left: 0px;
}
.tool-bar {
  background-color: #333;
  height: 100%;
  padding-top: 20px;
  display: none;
}

.comb {
  display: none;
}

.tips {
  text-align: center;
}

@media (min-width: 1000px) {
  .comb {
    display: block;
  }

  .tips {
    display: none;
  }
}

@media (min-width: 1250px) {
  .tool-bar {
    display: block;
  }
}
.combList {
  padding-top: 6px;
  padding-right: 10px;
}
#net-background {
  margin-top: 10px;
}
#add-icon {
  margin-left: 5px;
  margin-top: 10px;
  margin-right: 8px;
  font-size: 20px;
}
#minus-icon {
  margin-right: 10px;
  font-size: 20px;
}
.btn-primary {
  transition: all 0.3s ease;
  background: rgba(36, 41, 46, 0.9);
  border-color: rgba(36, 41, 46, 0);
  margin-top: 5px;
  color: white;
}
.btn-primary:active:focus {
  color: #333;
  background-color: #ffffff;
  border-color: rgba(36, 41, 46, 0);
}
.btn-primary:hover {
  color: #333;
  background-color: rgba(36, 41, 46, 0.2);
  border-color: rgba(36, 41, 46, 0);
}
.btn:active,
.btn:focus {
  z-index: 2;
  box-shadow: none;
  outline: none;
}

.control-label {
  margin-right: 5px;
}
.op-area {
  padding-left: 10px;
}
.radioColor {
  margin-left: 3px;
  margin-right: 5px;
}
#add-button {
  text-align: center;
  color: aliceblue;
  margin-top: 20px;
  padding-right: 10px;
  font-size: 18px;
  border: 1px aliceblue;
}
.comb-name {
  display: inline-block;
  width: 100%;
  color: #c4c3c3;
  padding-bottom: 0px;
  font-size: 18px;
  text-align: center;
}
.out-button {
  margin-top: 0px;
  margin-bottom: 10px;
  margin-left: 10px;
}
.select-icon {
  font-size: 15px;
}
</style>



